    margin-top: 5rem;
    overflow: hidden;
  }
}

.plan__inner {
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .plan__inner {
    overflow: visible;
  }
}

.plan__wrap {
  padding-top: 3.75rem;
}

.plan__lists {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: stretch;
}
@media screen and (min-width: 768px) {
  .plan__lists {
    width: 100vw;
    margin: 0 calc(50% - 50vw);
  }
}

.plan__item {
  width: 100%;
  background-color: #11315f;
  color: #fff;
  border-radius: 0.5rem;
  margin-left: 0.625rem;
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .plan__item {
    margin-left: 1.6875rem;
  }
}

.plan__image img {
  width: 100%;
  aspect-ratio: 322/190;
  object-fit: cover;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}
@media screen and (min-width: 768px) {
  .plan__image img {
    aspect-ratio: 322/210;
  }
}

.plan__content {
  padding: 1.1875rem 1.0625rem 3.125rem 1.0625rem;
}
@media screen and (min-width: 768px) {
  .plan__content {
    padding: 1.5rem 1.5625rem 3.125rem 1.5625rem;
  }
}

.plan__title {
  font-size: 1rem;
  line-height: 1.6875;
}

.plan__price {
  font-size: 1rem;
  line-height: 1.6875;
}
@media screen and (min-width: 768px) {
  .plan__price {
    margin-top: 4.125rem;
  }
}

.recommend__header {
  background-color: #2988b5;
  padding: 1.4375rem;
  color: #fff;
  font-size: 1.3125rem;
  text-align: center;
}

.recommend__lists {
  margin-top: 1.875rem;
}

.recommend__item {
  display: flex;
  flex-direction: row;
}

.recommend__item:nth-child(n+2) {
  margin-top: 1.875rem;
}

.recommend__image {
  width: 6.25rem;
  flex-shrink: 0;
}

.recommend__title {
  margin-left: 1.0625rem;
  font-size: 0.75rem;
  line-height: 2.4166666667;
}

.related__header {
  background-color: #2988b5;
  padding: 1.4375rem;
  color: #fff;
  font-size: 1.3125rem;
  text-align: center;
}

.related__lists {
  margin-top: 1.875rem;
}

.related__item {
  display: flex;
  flex-direction: row;
}

.related__item:nth-child(n+2) {
  margin-top: 1.875rem;
}

.related__image {
  width: 6.25rem;
  flex-shrink: 0;
}

.related__title {
  margin-left: 1.0625rem;
  font-size: 0.75rem;
  line-height: 2.4166666667;
}

.section-header {
  text-align: center;
}

.section-header__title {
  display: block;
  font-size: 2rem;
}

.section-header__subtitle {
  display: block;
  font-size: 1.25rem;
  font-weight: 700;
  font-family: "Times New Roman", sans-serif;
  text-transform: capitalize;
  margin-top: 2.125rem;
}
@media screen and (min-width: 768px) {
  .section-header__subtitle {
    margin-top: 2.5rem;
  }
}

.service {
  margin-top: 2.5rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  background-color: rgba(17, 49, 95, 0.1);
}

.service__lists {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
@media screen and (min-width: 768px) {
  .service__lists {
    flex-direction: row;
  }
}

.service__item {
  background-color: #11315f;
  color: #fff;
  padding: 3.125rem 1.0625rem;
  width: 100%;
}

.service__item:nth-child(n+2) {
  margin-top: 1.875rem;
}
@media screen and (min-width: 768px) {
  .service__item:nth-child(n+2) {
    margin-top: 0;
    margin-left: 1.25rem;
  }
}

.service__image {
  width: 6.875rem;
  margin: 0 auto;
}

.service__body {
  margin-top: 1.3125rem;
}

.service__title {
  font-size: 1.75rem;
  text-align: center;
  line-height: 1;
}

.service__text {
  font-size: 1.125rem;
  line-height: 1.4444444444;
  margin-top: 2.4375rem;
}

.top {
  background-image: url(/images/common/top-mv_sp.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
  position: relative;
}
@media screen and (min-width: 768px) {
  .top {
    background-image: url(/images/common/top-bg.jpg);
  }
}

.top::before {
  content: "";
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.top__title-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  text-align: center;
  font-size: 35px;
  color: #fff;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .top__title-wrap {
    font-size: 3.125rem;
  }
}

.top span {
  display: inline-block;
}

.article {
  margin-top: 5.875rem;
  margin-bottom: 7.5625rem;
}

.article__wrap {
  padding-top: 3.4375rem;
}
@media screen and (min-width: 768px) {
  .article__wrap {
    padding-top: 3.125rem;
  }
}

.article__category span {
  display: inline-block;
  background-color: #11315f;
  color: #fff;
  padding: 0.5625rem 2rem;
  font-size: 0.875rem;
}

.article__title {
  margin-top: 1.5625rem;
  font-size: 1.875rem;
}
@media screen and (min-width: 768px) {
  .article__title {
    margin-top: 2.75rem;
    font-size: 2.25rem;
  }
}

.article__flex {
  display: flex;
  flex-direction: column;
  margin-top: 1.5625rem;
}
@media screen and (min-width: 768px) {
  .article__flex {
    flex-direction: row;
    margin-top: 3.25rem;
  }
}

@media screen and (min-width: 768px) {
  .article__contents {
    width: 70%;
  }
}

.article__info {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.article__sns-wrap {
  display: inline-block;
  padding: 0.625rem 0.75rem;
  background-color: #00ACED;
  color: #fff;
  text-transform: capitalize;
  font-size: 1.25rem;
  font-weight: 700;
}

.article__sns i {
  width: 1.5625rem;
  margin-right: 0.625rem;
}

.article__date {
  font-size: 1rem;
}
@media screen and (min-width: 768px) {
  .article__date {
    font-size: 1.5rem;
  }
}

.article__image {
  margin-top: 1.875rem;
  overflow: hidden;
  position: relative;
}

.article__image:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #11315f;
  transition: 1s cubic-bezier(0.15, 0.65, 0.85, 0.65);
}

.article__image.c-open:before {
  transform: translate3d(100%, 0, 0);
}

.article__header1 {
  margin-top: 1.875rem;
  background-color: #11315f;
  color: #fff;
  font-size: 1.75rem;
  padding: 1.125rem 1.25rem;
}
@media screen and (min-width: 768px) {
  .article__header1 {
    margin-top: 2.625rem;
    font-size: 2.25rem;
    padding: 0.8125rem 1.375rem;
  }
}

.article__header2 {
  margin-top: 2.5rem;
  font-size: 1.625rem;
  padding: 0.625rem 1.3125rem;
  border-left: 0.125rem solid #2988b5;
}

.article__text1,
.article__text2 {
  margin-top: 2.3125rem;
  font-size: 1.125rem;
  line-height: 1.4444444444;
}
@media screen and (min-width: 768px) {
  .article__text1,
.article__text2 {
    font-size: 1.5rem;
    line-height: 1.0833333333;
  }
}

@media screen and (min-width: 768px) {
  .article__sidebar {
    width: 30%;
    padding-left: 3.75rem;
  }
}

.article__related {
  margin-top: 3rem;
}
@media screen and (min-width: 768px) {
  .article__related {
    margin-top: 0;
  }
}

.article__recommend {
  margin-top: 2.75rem;
}
@media screen and (min-width: 768px) {
  .article__recommend {
    margin-top: 2.375rem;
  }
}

.blog-archives {
  margin-top: 5rem;
  margin-bottom: 4.75rem;
}

.blog-archives__lists {
  margin-top: 3.125rem;
}

.blog-archives__item:nth-child(n+2) {
  margin-top: 1.875rem;
}
@media screen and (min-width: 768px) {
  .blog-archives__item:nth-child(n+2) {
    margin-top: 3.125rem;
  }
}

.blog-top {
  background-image: url(/images/blog/mv-sp.jpeg);
  background-size: cover;
  background-position: center;
  height: 24.375rem;
